html {
  font-size: 62.5%; /* 16px is default, 62.5% is 10px= 1rem */
  box-sizing: border-box; /*border-box avoids padding mess in similar sized elements */
}

.body {
  position: relative;
  font-size: 1.6rem;
  background-color: #080002;
  color: white;
  min-height: 100vh;
  max-width: 100vw;
  margin: 0;
}

.content-wrap {
  padding-bottom: 2.5rem;
}

/*jumbo and header styling */
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header-section {
  display: flex;
  justify-content: center;
  flex: 1 25vw;
}

.name-section {
  display: flex;
  justify-content: center;
  flex: 2 50vw;
}

@media only screen and (max-width: 760px) {
  .name-section {
    display: none;
  }
}

header menu {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 1vw;
  padding-inline-start: 0px;
}

header menu li {
  color: white;
  padding-right: 1vw;
  cursor: pointer;
}


header li a {
  text-decoration: none;
  color: white;
}

.name {
  align-self: center;
  font-weight: bold;
}

header ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  padding-left: 2vw;
}

header ul li {
  padding: 2vw;
  font-size: 2rem;
}

header ul li a {
  font-size: 3rem;
}


.border-wrap {
  background: white;
  padding: .25rem;
  border-radius: 100rem;
  display: flex;
  align-items: center;
}

.border-wrap:hover {
  background-image:linear-gradient(60deg, #3e35e8, #b41a63);
}

button { 
  background: #080002;
  font-size: 1.6rem;
  color: white;
  padding: 1rem;
  border-radius: 100rem;
}

button:hover  {
  color: white;
  background-image:linear-gradient(60deg, #3e35e8, #b41a63);
  cursor: pointer;
}

.jumbo {
  height: 120vh;
  display: flex;
  justify-content: center;
}

.jumbo img {
  max-height: 100vh;
  padding-top: 10vh;
  width: auto;
  border-radius: 28%;
}

@media only screen and (max-width: 760px) {
  .jumbo img {
    max-width: 100vw;
    max-height: auto;
  }
}

/*about section styles */
#about {
  min-height: 100vh;
  display: flex;
  flex-direction: row;
}

#about img {
  max-width: 35vw;
  height: fit-content;
  padding: 5vw;
  border-radius: 18%;
}

@media only screen and (max-width: 760px) {
  #about {
    flex-wrap: wrap;
  }

  #about img {
    max-width: 90vw;
    align-self: center;
    padding: 2vw;
    height: auto;
  }
  .personal-info {
    margin: 2vw;
  }
}

.personal-info {
  margin-right: 1vw;
}

#about ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

#about ul li {
  padding: 2vw;
  font-size: 4rem;
}

#about ul li:last-child {
  font-size: 2rem;
}

/*projects component styles */

.projects-container {
  width: 80%;
  margin: auto;
}

.project-box {
  position: relative;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 2%;
  border: .1rem solid white;
  border-radius: 5%;
}

.project-video {
  max-width: 90%;
  height: auto;
}

.project-btn-box {
  padding: 1.6rem;
  display: flex;
  justify-content: space-between;
  width: 80%;
}

.description-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.stack-box {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-around;
  font-size: 2.4rem;
}

.link-box {
  padding: 1.6rem;
  display: flex;
  justify-content: space-between;
  width: 40%;
}

.link-wrap {
  background: white;
  padding: .25rem;
  display: flex;
  align-items: center;
}

.link-wrap:hover {
  background-image:linear-gradient(60deg, #3e35e8, #b41a63);
}

.link-wrap a { 
  text-decoration: none;
  background: #080002;
  font-size: 1.6rem;
  color: white;
  padding: 1.6rem;
  border: .2rem solid black;
}

.link-wrap a:hover  {
  color: white;
  background-image:linear-gradient(60deg, #3e35e8, #b41a63);
}





/*fortune teller styles */
.fortune-container {
  width: 80%;
  margin: auto;
}

.fortune-box {
  position: relative;
  text-align: center;
  display: flex;
  padding: 5%;
  justify-content: center;
  margin-top: 1vh;
  border: .1rem solid white;
  border-radius: 5%;
  margin-bottom: 2.4rem;
}

.background-img {
  max-width: 60%;
  height: auto;
}

.fortune {
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  font-size: 2rem;
  width: 54%;
  background-color: black;
  color: white;
  opacity: 0.8;
}

.btn-box {
  display: flex;
  justify-content: center;
  padding-bottom: 1rem;
}


/* Footer styles */
.footer {
  margin-top: 2.4rem;
  display: flex;
  flex-direction: row;
  height: 20vh;
  width: 100%;

}

#contact {
  border-right: .1rem solid white;
  padding-left: 2rem;
  width: 50%;
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 760px) {
 #contact {
  display: none;
 }
}

#contact ul {
  list-style-type: none;
}

.socials {
  border-left: .1rem solid white;
  min-width: 50%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.socials a {
  text-decoration: none;
  color: white;
  font-size: 4rem;
}

/*alt theme styles */
.light-body {
  position: relative;
  font-size: 1.6rem;
  background-color: #e6e3dd;
  color: rgb(55, 58, 54);
  min-height: 100vh;
  max-width: 100vw;
  margin: 0;
}

.light-header {
  background-image: url("../public/img/jumbo.jpg");
  background-size: cover;
  background-position: bottom;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  height: 90vh;
}

.light-navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: rgba(230, 227, 221, 0.5);
    position: relative;
    height: 10vh;
    z-index: 1;
}

#light-link {
  text-decoration: none;
  color: rgb(55, 58, 54);
  padding-right: 1vw;
}

#light-link:hover {
  color: #d48166;
  cursor: pointer;
}

.display {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #e6e3dd;
  padding-bottom: 5vh;
}

.display h1 {
  font-size: 4.8rem;
}

.light-personal-info {
  padding: 5vw;
}

/* button styles */
.light-border-wrap {
  background: rgb(55, 58, 54);
  opacity: 0.9;
  padding: .25rem;
  display: flex;
  align-items: center;
}

.light-border-wrap:hover {
  background: #d48166;
  opacity: 1;
}

.light-border-wrap button { 
  background: #d48166;
  font-size: 1.6rem;
  color: #e6e3dd;
  padding: 1rem;
  opacity: 1;
  border-radius: 0rem;
}

.light-border-wrap button:hover  {
  cursor: pointer;
}

.light-border-wrap a {
  text-decoration: none;
  background: #d48166;
  font-size: 1.6rem;
  color: #e6e3dd;
  padding: 2rem;
  opacity: 1;
  border: .2rem solid black;
  border-radius: 0rem;
}

.light-border-wrap a:hover  {
  cursor: pointer;
}

/*light project styles */
.light-project-box {
  position: relative;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 2%;
  border: .1rem solid black;
  background-color: rgb(55, 58, 54);
  border-radius: 5%;
}

.light-project-box h1 {
  color: #e6e3dd;
}

.light-project-box h3 {
  color: #e6e3dd;
}

.light-description-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color:#e6e3dd;
}

.light-stack-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color:#e6e3dd;
}

#project-btn {
  background: #d48166;
  font-size: 1.6rem;
  color: #e6e3dd;
  padding: 1rem 2.4rem;
  border-radius: 100rem;
}

#project-btn:hover {
  cursor: pointer;
  background: #e6e3dd;
  color: #d48166;
}

.light-theme-fortune {
    background: rgb(55, 58, 54);
    border: .2rem solid black;
    border-radius: 5%;
}

.light-fortune-box {
  position: relative;
  text-align: center;
  display: flex;
  padding: 5%;
  justify-content: center;
  margin-top: 1rem;
}

.light-btn-box {
  display: flex;
  justify-content: center;
  margin-top: 2%;
  margin-bottom: 3%;
}

#light-fortune-button {
  border: .2rem solid black;
  border-radius: 100rem;
  background: #d48166;
  color: #e6e3dd;
  font-size: 2.4rem;
  cursor: pointer;
  padding: 1.6rem;
}

#light-fortune-button:hover {
  border: .2rem solid #d48166;
  background: #e6e3dd;
  color: #d48166;
}
